<template>
    <div class="login-container">
        <el-card header="请登陆后操作">
            <el-form v-model="model" @submit.native.prevent="login">
                <el-form-item label="用户名">
                    <el-input v-model="model.username"></el-input>
                </el-form-item>
                <el-form-item label="密码">
                    <el-input type="password" v-model="model.password"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" native-type="submit">登陆</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
    data(){
        return{
            model:{

            }
        }
    },
    methods:{
        async login(){
            const res = await this.axios.post('/login',this.model)
            const {token,username} = res.data
            if(token){
                localStorage.setItem('token',token)
                this.$router.push('/')
                this.$message({
                    tpye:'success',
                    message:`欢迎你，管理员${username}`,
                })
            }
        }
    }
}
</script>

<style lang="scss">
.login-container{
    width: 30rem;
    margin: 6rem auto;
}
</style>>
